import React, {useState, useContext, useEffect} from "react"

import { GlobalContext } from "../context/GlobalContext";

import { useHistory, useParams } from "react-router";

const TaskForm = () => {



//=================ESTADO=================//

//========== usando Golbalcontext para mandar los estados a los demas componentes============//
const  {addTask, tasks, updateTask}   = useContext(GlobalContext);

const history = useHistory();

const params = useParams();


const [task, setTask] = useState({
    id: "",
    title: "",
    description: "",
    done: "false"
});


//==============================================//


//==================funciones========================//

const handleChange = (e) => {
     // console.log(e.target.name, e.target.value);
    setTask({...task, [e.target.name]: e.target.value})
};



const handleOnSubmit = e => {
    e.preventDefault()
    // console.log(e)
    // console.log(task)
    if( task.title === "" &&  task.description === "" ) return null; 
    else {

    
    if (task.id) {
        updateTask(task)
      
    } else {
       

        addTask(task)

    }

    

    history.push("/")
}
}





useEffect(() => {

const taskFound = tasks.find( (task) => task.id == params.id);

    console.log("params.id = ",params.id);
    console.log("tasks = ",tasks);
    console.log("taskFound = ",taskFound);

    if (taskFound) { 
        console.log("editing....");
        setTask(taskFound)
    } else {
        console.log("creating....");
    }  

}, [params.id,tasks])





    return(


            <div className="flex justify-center items-center h-3/4">
                <form className="bg-gray-800 p-10 rounded-b-2xl" onSubmit={ handleOnSubmit }>
                    <h2 className="mb-7 "> 

                    {  task.id ? "Edit a Taks" : "Create a Task"}

                    </h2>
                    <div className="mb-5">
                        <input 
                            type="text" 
                            name="title" 
                            placeholder="Write a Title"
                            className="py-3 px-4  focus: outline-none focus:text-gray-100 bg-gray-700 w-full"
                            onChange={ handleChange }
                            value={task.title}
                        />
                    </div>


                    <div className="mb-5">
                        <textarea 
                        name="description"  
                        placeholder="Task Description" 
                        value={task.description}
                        rows="2" 
                        className="py-3 px-4  focus: outline-none focus:text-gray-100 bg-gray-700 w-full" 
                        onChange={ handleChange }></textarea> 
                       
                    </div>


                    <button className="bg-green-600 w-full hover:bg-green-500 py-2 px-4 rounded-lg">
                          {  task.id ? "+ Edit a Taks" : "+ Create Task"}  
                    </button>







                </form>
            </div>

    )
}

export default TaskForm

